import { useEffect, useState } from "react";
import './App.css';
import { getParamByISO } from 'iso-country-currency'
import arrow from './arrow.svg'

function App() {
  const [to, setTo] = useState("USD");
  const [from, setFrom] = useState(getParamByISO(window.navigator.language || navigator.language || navigator.userLanguage, 'currency'));
  const [amount, setAmount] = useState("1");
  const [res, setRes] = useState("0");
  const [list, setList] = useState({});


  let myHeaders = new Headers();
  myHeaders.append("apikey", "4iFmA11FGDejfy5ZDbfig7wIvEDE5Yqp");

  let requestOptions = {
    method: 'GET',
    redirect: 'follow',
    headers: myHeaders
  };

  useEffect(() => {
    fetch("https://api.apilayer.com/currency_data/list", requestOptions).then((res) => {
      return res.json();
    }).then((data) => {
      setList(data.currencies);
    })
  }, [])


  useEffect(() => {
    fetch('https://api.apilayer.com/currency_data/convert?to=' + to + '&from=' + from + '&amount=' + amount + '', requestOptions).then((res) => {
      return res.json()
    }).then((data) => {
      setRes(data.result)
    })
  }, [to, from, amount])

  return (
    <div className="App convertConteiner">
      <div className="convertFrom convertItem">
        <input type="number" value={amount} onChange={(data) => { setAmount(data.target.value) }}></input>
        <select value={from} onChange={(data) => { setFrom(data.target.value) }}>
          {Object.keys(list).map((key, item) =>
            <option value={key}>{key}</option>
          )}
        </select>
      </div>
      <button className="convertSwap" onClick={()=>{let save = to;setTo(from);setFrom(save)}}><svg width="24px" height="24px" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path d="m19.235 13.234h-14.47v3.494l-4.765-4.764 4.765-4.765v3.494h14.47v-3.494l4.765 4.765-4.765 4.765z"/></svg></button>
      <div className="convertTo convertItem">
        <input type="number" value={res}></input>
        <select value={to} onChange={(data) => { setTo(data.target.value) }}>
          {Object.keys(list).map((key, item) =>
            <option value={key}>{key}</option>
          )}
        </select>
      </div>
    </div>
  );
}
export default App;
